.section-hero {
  padding: 20px 0;

  .box-slider {
    width: 90%;
  }
}

.hero-flex-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-title {
  margin-bottom: 20px;
  font-family: var(--main-header-font);
  font-weight: 700;
  font-size: 28px;
  line-height: 1.2;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  color: var(--main-cl);
}
